.container{width:400px;height:200px;position:absolute;top:580px;left:10px}.container .music{overflow:hidden;position:relative;margin-top:10px}.container .songalbum{margin-left:50px;margin-top:0;width:71px;height:71px;border-radius:35.5px;background-color:#ffa4a4;overflow:hidden;border:5px solid #fff;box-shadow:0px 2px 10px 0px rgba(0,0,0,0.25);animation:rotate 10s linear infinite;background-image:url(../../assets/album.png)}.container .songalbum .mask{position:relative;width:100%;height:100%;background-color:rgba(191,191,191,0.582);opacity:0}.container .songalbum:hover{animation-play-state:paused}.container .songalbum:hover .mask{opacity:1}.container .songalbum .albumbg{position:relative;margin-top:2px}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.container .songnames .songtext{font-size:14px;color:#fff;position:absolute;margin-top:-60px;margin-left:130px}
